import {
  SET_ALERT,
  REMOVE_ALERT
} from '../actions/types';

const initialState = {
  alert: null  // alert object to be displayed on the screen.
};

export default function Alert(state = initialState, action) {
  const { type, payload } = action;

  switch(type) {
    case SET_ALERT :
        return {
            ...state,
            alert: payload
        };
    case REMOVE_ALERT:
        return{
            ...state,
            alert: null
        }
        default:
            return state
  };
}